<template>
  <section class="wrapper">
    <form class="new-todo-form">
      <label class="new-todo-label">
        New Todo:
        <input v-model="newTodo" type="text" class="new-todo-input" />
      </label>
      <button type="submit" @click.prevent="addTodo()" class="new-todo-button">
        Add
      </button>
    </form>
    <ul class="todo-list">
      <li v-for="todo in todoList" :key="todo.id" class="todo-item">
        <label class="todo-item-label">
        <button type="cancel" @click.prevent="deleteTodo(todo.id)">X</button>
          {{ todo.name }}
        </label>
        <input
            type="checkbox"
            v-model="todo.done"
            @change="updateTodo(todo)"
            class="todo-item__checkbox"
          />
      </li>
    </ul>
  </section>
</template>

<script>
import { todosCollection } from "./../db";
export default {
  data() {
    return {
      newTodo: "",
      todoList: [],
    };
  },
  firestore() {
    return {
      todoList: todosCollection.orderBy("createdAt", "desc"),
    };
  },
  methods: {
    async addTodo() {
      todosCollection
        .add({
          name: this.newTodo,
          done: false,
          createdAt: new Date(),
        })
        .catch(function(error) {
          console.error("Error adding document: ", error);
        });
      this.newTodo = "";
    },
    async updateTodo(todo) {
      todosCollection.doc(todo.id).update({ ...todo });
    },
    async deleteTodo(id) {
      todosCollection.doc(id).delete();
    },
  },
};
</script>

